.sidebar {
  position: fixed;
  background-color: #2d2d2d;
  height: 100% !important;
  width: 420px;
  left: -320px;
  -webkit-transition: left 0.3s ease;
  -moz-transition: left 0.3s ease;
  -ms-transition: left 0.3s ease;
  -o-transition: left 0.3s ease;
  transition: left 0.3s ease;
  z-index: 3;
  &.active {
    left: 0;
  }
}

.scroll-top {
  opacity: 0;
  visibility: hidden;
  line-height: 0;
  z-index: 2;
  overflow: hidden;
  margin-left: 34px;
  bottom: 35px;
  &.show {
    opacity: .9;
    visibility: visible;
  }
  &:hover {
    cursor: pointer;
  }
  .fa {
    font-size: 36px;
    width: 32px;
    color: #0697ae;
    &:hover {
      color: #47afbf;
    }
  }
}

#header-main-menu {
  font-family: "Montserrat", sans-serif, Arial;
  padding-left: 50px;
  padding-top: 20px;
}

.menu-left-part {
  width: 320px;
  position: absolute;
  background: #333333;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 3;
}

.menu-right-part {
  float: right;
  width: 100px;
  min-height: 100%;
  background-color: #242424;
}

#toggle {
  height: 60px;
  padding: 20px 0;
  position: relative;
  background-color: #333333;
  &:hover {
    cursor: pointer;
  }
  &:before,
  &:after {
    content: "";
    height: 4px;
    width: 26px;
    background: #5b5b5b;
    left: 36px;
    position: absolute;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-transition: -webkit-transform 0.25s;
    transition: transform 0.25s;
    top: 48px;
  }
  &:before {
    -webkit-transform: translate3d(0, -8px, 0);
    transform: translate3d(0, -8px, 0);
  }
  &:after {
    -webkit-transform: translate3d(0, 8px, 0);
    transform: translate3d(0, 8px, 0);
  }
  &.on {
    &:before {
      -webkit-transform: rotate3d(0, 0, 1, 45deg);
      transform: rotate3d(0, 0, 1, 45deg);
    }
    &:after {
      -webkit-transform: rotate3d(0, 0, 1, -45deg);
      transform: rotate3d(0, 0, 1, -45deg);
    }
    .menu-line {
      opacity: 0;
    }
  }
  .menu-line {
    width: 26px;
    height: 4px;
    background: #5b5b5b;
    left: 36px;
    position: absolute;
    -webkit-transition: opacity 0.25s;
    -moz-transition: opacity 0.25s;
    -ms-transition: opacity 0.25s;
    -o-transition: opacity 0.25s;
    transition: opacity 0.25s;
    top: 48px;
  }
}

.social-list {
  text-align: center;
  color: #5b5b5b;
  font-size: 30px;
  a {
    padding-top: 80px;
    display: block;
    color: #5b5b5b;
    &:hover {
      color: #797979;
    }
  }
}
